// @flow
import React, { Component } from "react";

import styles from "./index.css";
import { View } from "react-web-dom";
import InfoRow from "../../public/info/infoRow";
import moment from 'moment'

type Props = {
    sn: string,
    reciver_name: string,
    create_time: number,
    trade_no:string,
    state: number
}
type State = {}
export default class OrderDetailBasicInfo extends Component<Props, State> {
    render() {
        const { sn, create_time, state,trade_no } = this.props
        return (
            <View className={styles.infoWarp}>
                <p className={styles.infoTitle}></p>
                <InfoRow
                    infoList={[
                        {
                            title: '',
                            info: sn,
                        }
                        // , {
                        //     title: '',
                        //     info: reciver_name,
                        //     // typeLink: 1,
                        // }
                        , {
                            title: '',
                            info: moment(create_time, 'X').format('YYYY-MM-DD HH:mm:ss'),
                        }, {
                            title: '',
                            info: this.returnOrderState(state),
                        },
                        {
                            title: '',
                            info: trade_no,
                        }
                    ]}
                />
            </View>
        );
    }

    returnOrderState(state: number) {
        switch (state) {
            case 0:
                return ''
            case 10:
                return ''
            case 20:
                return ''
            case 30:
                return ''
            case 40:
                return ''
            default:
                return ''
        }
    }
}
